  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  height: 100%; }

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  background-color: #f9fbfd; }

a {
  color: #011c40;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: 100%; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.page {
  scroll-behavior: smooth; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh)); }

main {
  flex: 1 1 auto; }

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */ }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.scroll-lock-ios {
  position: fixed;
  overflow: hidden; }

.scroll-lock {
  overflow: hidden; }

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .no-scrollbar::-webkit-scrollbar {
    display: none; }

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important; }

.page-body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative; }

.page-body.shadow::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 43, 102, 0.5); }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 350px; }
  @media (max-width: 1023px) {
    .container {
      max-width: 1023px;
      padding: 0 50px; } }
  @media (max-width: 767px) {
    .container {
      max-width: 767px;
      padding: 0 25px; } }

.btn {
  display: inline-flex;
  margin: 10px 0;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease; }
  .btn--red {
    background-color: #ff1553; }
  .btn:disabled {
    opacity: 0.5;
    pointer-events: none; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .btn:hover, .btn:focus {
      opacity: 0.8; } }

.header {
  position: fixed;
  width: 100%;
  color: #F2F5F6;
  min-height: 120px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  z-index: 10; }

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px; }

/* Header__logo */
.header__logo {
  display: flex; }

.logo__image {
  display: inline-block;
  vertical-align: middle;
  width: 55px;
  height: 55px;
  margin-right: 15px; }

.logo__text {
  display: flex;
  flex-direction: column; }

.logo__text p {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  font-weight: 700;
  line-height: 29px;
  margin: 0;
  padding: 0; }

.logo__text span {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  color: #ffffff;
  opacity: 0.7; }

/* Callback-contact */
.callback-contact__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  column-gap: 120px; }

.callback-contact__link {
  position: relative;
  color: #F2F5F6;
  padding: 3px 0 3px 35px;
  line-height: 18px; }
  .callback-contact__link::before {
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    left: 0;
    top: 50%;
    transform: translateY(-50%); }
  .callback-contact__link--mail::before {
    background-image: url("../img/svg/email.svg");
    background-repeat: no-repeat;
    background-position: 0 0; }
  .callback-contact__link--phone::before {
    background-image: url("../img/svg/phone-call.svg");
    background-repeat: no-repeat;
    background-position: 0 0; }
  @media (min-width: 1024px) {
    .callback-contact__link:hover, .callback-contact__link:focus {
      color: #00B2FF; }
    .callback-contact__link:active {
      color: #0d7aee; } }

/* Header__button */
.header__button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: transparent;
  color: #F2F5F6;
  border: 1px solid #F2F5F6;
  border-radius: 3px;
  margin: 0;
  padding: 11px 16px;
  margin-right: 60px;
  cursor: pointer;
  transition: background-color 0.3s ease; }
  @media (min-width: 1024px) {
    .header__button:hover, .header__button:focus {
      background-color: #00B2FF; }
    .header__button:active {
      background-color: #1600ff; } }
  .header__button:active {
    background-color: #1600ff; }

.intro {
  color: #F2F5F6;
  background-image: url("../img/background/intro-background.png");
  background-color: #011c40;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover;
  min-height: 760px;
  margin-top: 103px; }

.intro__block {
  padding-top: 202px;
  margin-left: 126px; }

.intro__block h1 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 40px; }

.intro__block p {
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  max-width: 685px;
  margin: 0;
  margin-bottom: 40px; }

.intro__block a {
  display: inline-block;
  vertical-align: middle;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #F2F5F6;
  background-color: #00B2FF;
  border-radius: 3px;
  padding: 15px 25px;
  transition: background-color 0.3s ease; }
  @media (min-width: 1024px) {
    .intro__block a:hover, .intro__block a:focus {
      background-color: #382f97; }
    .intro__block a:active {
      background-color: #1600ff; } }
  .intro__block a:active {
    background-color: #1600ff; }

.advantages__container {
  margin: 120px auto; }

/* ============ advantages__list */
.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px; }

/* ============ advantages__item */
.advantages__item {
  position: relative;
  display: flex;
  flex-direction: column;
  border: 1px solid #EBEBEB;
  padding: 40px 45px 35px;
  cursor: pointer; }
  .advantages__item::before {
    position: absolute;
    content: "";
    width: 5px;
    height: 100%;
    background-color: #00B2FF;
    left: 0;
    top: 0;
    transition: width 0.8s ease; }

.advantages__item h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #00B2FF;
  margin: 0;
  margin-bottom: 15px;
  transition: color 0.8s ease; }

.advantages__item p {
  line-height: 22px;
  color: #041827;
  margin: 0;
  transition: color 0.8s ease; }

.advantages__item a {
  display: inline-block;
  vertical-align: middle;
  width: 35px;
  height: 35px;
  order: -1;
  margin-bottom: 20px; }

.advantages__item svg {
  fill: #00B2FF;
  width: 100%;
  height: 100%;
  transition: fill 0.8s ease; }

/* Эффект при наведении на карточку с преимуществами */
.advantages__item:hover::before {
  width: 100%; }

.advantages__item:hover h3,
.advantages__item:hover p {
  position: relative;
  z-index: 2;
  color: #F2F5F6; }

.advantages__item:hover svg {
  position: relative;
  z-index: 2;
  fill: #F2F5F6; }

